import fs from 'fs';
import path from 'path';
import { exec } from 'child_process';

class ES6 {

    constructor (commander) {
        commander
            .option('-c, --cli', 'Command Line/Server application')
            .option('-b, --babel', 'Enable Babel');
    }

    execute (commander) {
        let name = typeof commander.name != 'function' ? commander.name : `es6-project`;

        if (fs.existsSync(commander.name)){
            console.warn(`Folder ${name} already exists. Specify a different name.`)
            process.exit();
        }

        //Create project folder
        fs.mkdirSync(`./${name}`);

        //Copy template project to folder
        console.log(`${path.resolve(__dirname)}/projects/es6/`);
        exec(`cp -r ${path.resolve(__dirname)}/projects/es6/* ./${name}/`);
    }

}

export default ES6


